<template>
	<view>
		<view class="charts-box">
			<qiun-data-charts type="tline" :opts="{yAxis:{data:[{min:0,max:80}]}}" :chartData="chartsDataTLine" />
		</view>
	</view>
</template>

<script>
	var TrafficStats = plus.android.importClass("android.net.TrafficStats"); 
	export default {
		name: "network-chart",
		props: {
			pageScrollTop: {
				type: Number,
				default: 0
			}
		},
		data() {
			return {
				chartsDataTLine: {},
				total_data,    //总共接收到的流量  
				traffic_data,  //一定时间内接收到的流量  
				intervalId,    //定时器的返回值，用于控制计时器的停止  
			};
		},
		//新手注意了，组件里没有onLoad事件！！！
		mounted() {
			this.getServerData()
		},
		methods: {
			getServerData() {
				const that=this;
				this.chartsDataTLine = {
					"series": [{
						"name": "时间",
						"data": [
							[10000, 55],
						]
					}]
				}
				
				document.addEventListener('plusready', function(){
				    //console.log("所有plus api都应该在此事件发生后调用，否则会出现plus is undefined。"  
				     TrafficStats = plus.android.importClass("android.net.TrafficStats");  
				    that.total_data = TrafficStats.getTotalRxBytes();  
				    that.intervalId = window.setInterval("getNetSpeed()", 1000);   
				});  
			},
			getNetSpeed(){
			    that.traffic_data = TrafficStats.getTotalRxBytes() - total_data;  
			    that.total_data = TrafficStats.getTotalRxBytes();  
			    console.log(that.bytesToSize(that.traffic_data));  
			    console.log(that.bytesToSize(that.total_data));  
			},
			//将byte自动转换为其他单位  
			  bytesToSize(bytes) {
			      if (bytes === 0) return '0 B/s';  
			      var k = 1000, // or 1024  
			          sizes = ['B/s', 'KB/s', 'MB/s', 'GB/s', 'TB/s', 'PB/s', 'EB/s', 'ZB/s', 'YB/s'],  
			          i = Math.floor(Math.log(bytes) / Math.log(k));  
			     return (bytes / Math.pow(k, i)).toPrecision(3) + ' ' + sizes[i];  
			  }   
		}
	}
</script>

<style>
	.charts-box {
		width: 100%;
		height: 300px;
	}
</style>
